<template>
  <div id="login-page">
    <div class="login-box">
      <p class="login-title">系统登录</p>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
        <el-form-item prop="loginUserName">
          <el-input placeholder="用户名" v-model="ruleForm.loginUserName"></el-input>
        </el-form-item>
        <el-form-item prop="loginUserPassword">
          <el-input placeholder="密码" v-model="ruleForm.loginUserPassword" show-password></el-input>
        </el-form-item>
        <el-button type="primary" class="login-btn" @click="login(ruleForm.loginUserName)">登录</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import { asyncRouter } from "@/router/index";
import request from "@/http/request";
export default {
  data() {
    return {
      ruleForm: {
        loginUserName: "admin",
        loginPassword: "",
      },
      rules: {
        loginUserName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        loginUserPassword: [
          { required: true, message: "请输入密码", trigger: "blur" }
        ]
      },
    };
  },
  watch:{
    'ruleForm.loginUserName'(value){
     this.ruleForm.loginUserName = value;
    }
  },
  methods: {
    login(userName) {
      let temRouter = [];
      if (this.$store.state.permissions.length === 0) { //当用户未登录 本地无数据时
        if (userName === "admin") { //根据不同权限配置动态路由
          this.$store.commit("chPermissions", "admin");
          asyncRouter.forEach(router => {
            if (router.meta.role[0] === "admin") {
              temRouter.push(router);
            }
          });
        } else {
          this.$store.commit("chPermissions", "visitor");
          asyncRouter.forEach(router => {
            if (router.meta.role[0] === "visitor") {
              temRouter.push(router);
            }
          });
        }
      
        request.http_mock("http://myapp/login", this.loginUserName).then(res => { //post请求
        localStorage.setItem("token", res._data[0].token);  //本地存储token和权限 并添加动态路由
        localStorage.setItem("permissions",this.ruleForm.loginUserName);
        this.$router.options.routes.push(...temRouter);
        this.$router.addRoutes(temRouter);
        this.$router.push("/");  //跳转至首页
      });
      }
      this.changeInput = false;
      this.$router.push("/"); 
    }
  }
};
</script>

<style>
#login-page {
  width: 100%;
  height: 100%;
  background-color: rgb(38, 38, 51);
}
.login-box {
  position: relative;
  top: 25%;
  width: 500px;
  height: 500px;
  margin: 0 auto;
}
.login-title {
  margin: 0;
  padding: 10px 0 40px 0;
  color: aliceblue;
  font-size: 25px;
  font-weight: 600;
  text-align: center;
}
.el-form-item__content {
  margin-left: 0 !important;
  font-size: 18px;
}
.el-form-item__content input {
  height: 60px;
  color: aliceblue;
}
.el-input__inner {
  background-color: rgb(36, 50, 61);
}
.login-btn {
  width: 100%;
  margin-top: 10px;
}
</style>